<template>
    <div class="rank">
        <van-tabs v-model="activeName" animated>
            <van-tab title="男频" name="male" @click="getRankDetail()">
                <div class="sub-rank">
                    <keep-alive>
                        <van-sidebar v-model="activeKeym" class="sidebar">
                            <van-sidebar-item v-for="item in male" :key="item._id" :title="item.shortTitle" @click="getRankDetail()"/>
                        </van-sidebar>
                     </keep-alive>
                    <div class="content">
                        <r-lists :list="list" />
                    </div>
                </div>
            </van-tab>
            <van-tab title="女频" name="female" @click="getRankDetail()">
                <div class="sub-rank">
                    <keep-alive>
                        <van-sidebar v-model="activeKeyf" class="sidebar">
                            <van-sidebar-item v-for="item in female" :key="item._id" :title="item.shortTitle" @click="getRankDetail()" />
                        </van-sidebar>
                    </keep-alive>
                    <div class="content">
                        <r-lists :list="list" />
                    </div>
                </div>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
import RLists from './components/RLists'
import { fetchRankDetail } from '@/api/rank'

export default {
    name: 'Rank',
    data() {
        return {
            activeName: 'male',
            activeKeym: 0,
            activeKeyf: 0,
            male: [
                { "_id": "54d42d92321052167dfb75e3", "shortTitle": "最热榜" },
                { "_id": "5a6844aafc84c2b8efaa6b6e", "shortTitle": "好评榜" },
                { "_id": "5a6844f8fc84c2b8efaa8bc5", "shortTitle": "热搜榜" },
                { "_id": "54d42e72d9de23382e6877fb", "shortTitle": "潜力榜" },
                { "_id": "564547c694f1c6a144ec979b", "shortTitle": "留存榜" },
                { "_id": "564eb878efe5b8e745508fde", "shortTitle": "完结榜" },
                { "_id": "57eb86f0ef9e5a8f20543d7d", "shortTitle": "VIP榜" }
            ],
            female: [
                { "_id": "54d43437d47d13ff21cad58b", "shortTitle": "最热榜" },
                { "_id": "5a684551fc84c2b8efaab179", "shortTitle": "好评榜" },
                { "_id": "5a684515fc84c2b8efaa9875", "shortTitle": "热搜榜" },
                { "_id": "54d43709fd6ec9ae04184aa5", "shortTitle": "潜力榜" },
                { "_id": "5645482405b052fe70aeb1b5", "shortTitle": "留存榜" },
                { "_id": "564eb8a9cf77e9b25056162d", "shortTitle": "完结榜" },
                { "_id": "57eb959df60eb7e21fb3a8b7", "shortTitle": "VIP榜" }
            ],
            list: []
        };
    },
    computed: {
        rankId() {
            let id = ''
            if(this.activeName === "male") {
                id = this.male[this.activeKeym]._id
            }
            if(this.activeName === "female") {
                id = this.female[this.activeKeyf]._id
            }
            return id
        }
    },
    components: {
        RLists
    },
    mounted () {
        this.getRankDetail()
    },
    methods: {
        // rankId(arr) {
        //     if(arr.toString() == this.male.toString()) {
        //         return this.male[this.activeKeym]._id
        //     }
        //     if(arr.toString() == this.female.toString()) {
        //         return this.female[this.activeKeyf]._id
        //     }
        // },
        getRankDetail() {
            fetchRankDetail(this.rankId).then(res => {
                this.list = res.data.ranking.books
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.rank {
    .sub-rank {
        display: flex;
        justify-content: start;
        .sidebar {
            width: 17%;
        }
        .content {
            width: 83%;
        }
    }
    
}
</style>


