<template>
    <div  class="category-list">
        <div
            v-for="(item, index) in list"
            :key="index"
            class="category-list-item"
            @click="toBookInfo(item._id)"
        >
            <div class="item-top">
                <img :src="item.cover | fixImg" />
            </div>
            <div class="item-bottom">
                <h2 class="textOverflow1">{{item.title}}</h2>
                <p class="textOverflow2">{{item.shortIntro}}</p>
                <div>
                    <span><i>{{(item.latelyFollower/10000).toFixed(1)}}万</i> 人气</span>
                    <span><i>{{parseFloat(item.retentionRatio).toFixed(1)}}%</i> 留存</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'RankDetail',
    props: {
        list: {
            type: Array
        }
    },
    methods: {
        toBookInfo(id) {
            this.$router.push({ name: 'BookInfo', params: { id: `${id}` }})
        }
    }
}
</script>

<style lang="scss" scoped>
.category-list {
        padding: 0 10px;
        padding-bottom: 50px;
        .category-list-item {
            display: flex;
            justify-content: start;
            padding: 10px 0;
            .item-top {
                img {
                    width: 75px;
                    height: 100px;
                    box-shadow: 0 2px 5px #ccc;
                }
            }
            .item-bottom {
                margin-left: 10px;
                h2 {
                    font-size: 16px;
                    font-weight: 700;
                    line-height: 22px;
                    padding-bottom: 10px;
                }
                p { 
                    line-height: 20px;
                    color: #999;
                }
                div {
                    padding-top: 10px;
                    color: #999;
                    span {
                        display: inline-block;
                        width: 50%;
                        i {
                        color: #ee0a24;
                    }
                    }
                }
            }
        }
    }
</style>